import React from 'react'
import { Typography, Grid , Button} from '@material-ui/core'
import { Link } from 'react-router-dom'

import useStyles from './styles'

const HomeCategory = () => {
    const classes = useStyles();
    return (
        <Grid container className={classes.homeMain} spacing={3 }>
            {/* title */}
            <Grid item xs={9}>
                <Typography variant='h5' align='center' gutterBottom>MODERN HOMES</Typography>
                <Typography variant='body2' align='center'>Check out our new furniture collection! Cozy sofa, fancy chair, wooden casket, and many more. The new collection brings an informal elegance to your home.</Typography>
            </Grid>
            {/* New Arrivals and Bestseller */}
            <Grid item xs={12} sm={5}>
                <img className={classes.mainImg} src={process.env.PUBLIC_URL + `/assets/carousel/newArrivals.jpg`} alt="" />
                <Typography variant='h6'>New Arrivals</Typography>
                <Button component={Link} to='/Shop' color='primary'>Shop Now</Button>
            </Grid>
            <Grid item xs={12} sm={5}>
                <img className={classes.mainImg} src={process.env.PUBLIC_URL + `/assets/carousel/bestSeller.jpg`} alt="" />
                <Typography variant='h6'>Best Seller</Typography>
                <Button component={Link} to='/Shop' color='primary'>Shop now</Button>
            </Grid>
            {/* Other sale off topics */}
            <Grid item xs={11} sm={3}>
                <img className={classes.mainImg} src={process.env.PUBLIC_URL + `/assets/carousel/springsales.jpg`} alt="" />
                <Typography variant='h6'>SPRING SALE</Typography>
                <Typography variant='overline' display='block'>Save up to 70%</Typography>
            </Grid>
            <Grid item xs={11} sm={3}>
                <img className={classes.mainImg} src={process.env.PUBLIC_URL + `/assets/carousel/summer.jpg`} alt="" />
                <Typography variant='h6'>SUMMER COLLECTION</Typography>
                <Typography variant='overline' display='block'>New Breeze in July</Typography>
            </Grid>
            <Grid item xs={11} sm={3}>
                <img className={classes.mainImg} src={process.env.PUBLIC_URL + `/assets/carousel/onlineexclusive.jpg`} alt="" />
                <Typography variant='h6'>ONLINE EXCLUSIVE</Typography>
                <Typography variant='overline' display='block'>Checkout Online Deals</Typography>
            </Grid>
        </Grid>
    )
}

export default HomeCategory
