import React from 'react'
import { Link } from 'react-router-dom'
import { Container, Typography, Button, Grid, Box} from '@material-ui/core'
import useStyles from "./styles"
import CartItem  from "./CartItem/CartItem"

const Cart = ({ cart , handleUpdateCartQty,handleRemoveFromCart, handleEmptyCart }) => {
    
    const classes = useStyles();

    const EmptyCart = ()=>(
        <Typography variant="overline" display='block' align='center'>
            No Item in your Cart, 
            <Link to="/" className={classes.link}>start adding some!</Link>
        </Typography>
    );

    const FilledCart = ()=>(
        <Box className={classes.cartBox}>
            <Grid container spacing={4} className={classes.cartContainer}>
                <Grid item xs={6}><Typography variant='h5'>ORDER</Typography></Grid>
                <Grid item xs={6} style={{textAlign: 'end'}}>
                    <Button className={classes.emptyButton} size="small" type="button" variant="text" color="secondary"
                            onClick={handleEmptyCart}>
                        Empty Cart
                    </Button>    
                </Grid>

                {cart.line_items.map((item) => (
                    <Grid item xs={12} key={item.id}>

                        <CartItem item={item} 
                        onUpdateCartQty = {handleUpdateCartQty} 
                        onRemoveFromCart = {handleRemoveFromCart}/>

                    </Grid>
                ))}

                <Grid item xs={12}>
                    <Typography variant="h5" gutterBottom >
                       <b>Subtotal:</b>  {cart.subtotal.formatted_with_symbol}
                    </Typography>

                    
                    <Button className={classes.checkoutButton} size="medium" type="button" variant="contained" color="primary"
                            component={Link} to="/checkout">
                        Checkout
                    </Button>
                </Grid>
            </Grid>
            
        </Box>
    );

    if(!cart.line_items) return "Loading..."
    return (
        
        <Container>
            
            <div className={classes.toolbar}/>
            <Typography className={classes.title} variant="h3" align='center' gutterBottom>
                Your Shopping Cart
            </Typography>
            {!cart.line_items.length ? <EmptyCart /> : <FilledCart />}
        </Container>
    )
}

export default Cart
