import React from 'react';
import { Typography, Button,IconButton, Card, CardActions, CardContent, CardMedia, Grid } from '@material-ui/core';
import { DeleteForever } from '@material-ui/icons';
import useStyles from "./styles";


const CartItem = ({item, onUpdateCartQty, onRemoveFromCart}) => {
    const classes = useStyles();
    return (
        <>
        
        <hr/>
        <Grid container className={classes.cardWrapper}>
            
            <Grid item xs={2}>
                <img className={classes.media} src={item.media.source} alt={item.name} />
            </Grid>
            <Grid item xs={6} className={classes.cardContent}>
                <Typography variant="Button"><b>{item.name.toUpperCase()}</b></Typography>
                <Typography variant="overline">{item.line_total.formatted_with_symbol}</Typography>
            </Grid>
            <Grid item xs={4} className={classes.cardActions}>
                <div className={classes.buttons}>
                    <Button type="button" size="small" onClick={() => onUpdateCartQty(item.id, item.quantity -1)}>-</Button>
                    <Typography>{item.quantity}</Typography>
                    <Button type="button" size="small" onClick={() => onUpdateCartQty(item.id, item.quantity +1)}>+</Button>

                    <IconButton aria-label='delete' variant="contained" type="button"  onClick={()=> onRemoveFromCart(item.id)}>
                        <DeleteForever />
                    </IconButton>
                </div>
                
            </Grid>
        </Grid>
        </>
        
    )
}

export default CartItem
