import React, {useState, useEffect} from 'react'
import { CircularProgress, Typography, Grid, ListItem, Hidden, CssBaseline, ButtonGroup, Button } from '@material-ui/core'
import { commerce } from '../../lib/commerce'

import HomeCarousel from '../HomePage/HomeCarousel/HomeCarousel'
import Products from '../Products/Products'
import useStyles from './styles'

const Shop = ({defaultRender, onAddToCart}) => {
    const classes = useStyles();
    const categories = ['Living Room', 'Bedroom', 'Kitchen', 'Bathroom'];
    const [products, setProducts] = useState({});
    const [defaultCat, setDefaultCat] = useState('All Product')
    const [isLoading, setIsLoading] = useState(true)

    const fetchProducts = async (slug)=> {
        console.log(slug)
        const {data} = await commerce.products.list({
            category_slug: [slug],
        });
        
        setProducts(data);
    }
    const fetchAllProducts = async ()=> {
        setIsLoading(true)
        try{
            const {data} = await commerce.products.list({
                category_slug: ['homebasic'],
            });
            setProducts(data); 
        }
        catch (error) {
            console.error();
        }
        finally{
            setIsLoading(false) 
        }
         
    }

    
    useEffect( ()=>{
        
        if(defaultRender){
            const render = defaultRender.toLowerCase()
            fetchProducts(render)
            setDefaultCat(defaultRender)
        }else{
            fetchAllProducts();
        }
         
        return fetchAllProducts();
    }, []);

    
    const MainShop = ({catName = defaultCat, onAddToCart, products}) => (
        <>
        <Typography variant='h2'>{catName}</Typography>
        <Products products={products} onAddToCart={onAddToCart}/>
        </>
    )
    
    const handleCatClick = async (catName)=> {
        setDefaultCat(catName)
        setIsLoading(true)
        const slug = catName.replace(/\s+/g, '-').toLowerCase();
        
        
        try{
            fetchProducts(slug)
        }
        catch (error) {
            console.error();
        }
        finally{
            setIsLoading(false) 
        }
        
    }

    return (
        <>
            <div className={classes.toolbar} />
            <HomeCarousel typoDisplay='NEW ITEM ARRIVED'/> 

            <Grid container spacing={1}>
                <Hidden xsDown>
                <Grid item xs={2} className={classes.drawer}>
                    <ButtonGroup className={classes.drawerItems}
                     orientation="vertical"
                     aria-label="vertical contained primary button group"
                     variant="text">

                        {categories.map((cat) => (
                            <Button  onClick={()=> handleCatClick(cat)} key={cat} >{cat}</Button>
                        ))}

                    </ButtonGroup> 
                </Grid>
                </Hidden>

                <Hidden smUp>
                <Grid item xs={12} className={classes.drawer}>
                        {categories.map((cat) => (
                            <Button onClick={()=> handleCatClick(cat)} key={cat}>{cat}</Button>
                        ))}
                </Grid>
                </Hidden>

                <Grid item xs={12} sm={10} className={classes.mainShop}>
                    
                {isLoading ?  <CircularProgress className={classes.spinner}/> : <MainShop products={products} onAddToCart={onAddToCart}/>  }
                </Grid>
            </Grid>

        </>
    )
}

export default Shop
