import React from 'react'
import { Typography, Link } from '@material-ui/core'
import { Facebook, GitHub, PhoneIphone, Email } from '@material-ui/icons'

import useStyles from './styles'
const Contact = () => {
    const classes = useStyles()
    return (
        <div className={classes.wrapper}>
            <div className={classes.toolbar}/>
            <div className={classes.toolbar}/>
            
            <Typography variant='h3'>Thank you for your time !</Typography>
            <Typography variant='overline' align='center' gutterBottom>
                I always love to have a opportunities to work and go as far as I can. <br />
                I hope you can hit me at Contacts below. 
            </Typography>
            <div>
               <Link color='textPrimary' href='tel:+1 (865) 347 9653'> <PhoneIphone /> </Link>
               <Link color='textPrimary' href='mailto:dante.mai96@gmail.com'> <Email /> </Link>
               <Link color='textPrimary' href='https://www.facebook.com/dante0328'> <Facebook /> </Link>
               <Link color='textPrimary' href='https://github.com/se64vet'> <GitHub /> </Link>
            </div>
            <div>
                <img src={process.env.PUBLIC_URL + './assets/Instagram/3.jpg'} alt="Contact for works" />
            </div>

            <div>
            <Typography variant='body2'>THIS WEBSITE IS CREATED BY ME WITH INSPIRATIONS FROM</Typography>
            <Typography variant='caption' display='block'>https://www.luluandgeorgia.com/</Typography>
            <Typography variant='caption' display='block'>https://flatlogic-ecommerce.herokuapp.com/</Typography>
            <Typography variant='caption' display='block'>Javascript Mastery</Typography>
            </div>
        </div>
    )
}

export default Contact
