import React from 'react';
import { Card, CardMedia, CardContent, CardActions, Typography, IconButton , Grid} from '@material-ui/core';
import { AddShoppingCart } from '@material-ui/icons'

import useStyles from "./styles"

const Product = ({ product, onAddToCart }) => {
    const classes = useStyles();
    const {categories} = product;
    return (
        <Card className ={classes.root}>
            <CardMedia className = {classes.media} image = {product.media.source} title = {product.name} />
            <CardContent>
                <Grid container>
                    <Grid item key="1" xs={10}>
                        <div>
                            <Typography variant = "caption" className = {classes.cardContent} gutterBottom >
                                    {categories[0].name}
                                </Typography>
                            </div>
                            <div className = {classes.cardContent} >
                                <Typography variant = "h5" style={{fontWeight: 700}} >
                                    {product.name}
                                </Typography>
                            </div>
                            <div>
                            <Typography variant = "subtitle1" className = {classes.cardContent}>
                                    {product.price.formatted_with_symbol}
                            </Typography>
                        </div>
                    </Grid>
                    <Grid item key="2" xs={2} onClick={()=> onAddToCart(product.id, 1)} className={classes.cardActions}>
                        <IconButton aria-label ="Add to Cart" >
                        <AddShoppingCart />
                        </IconButton>
                    </Grid>
                </Grid>
                

                {/* <Typography dangerouslySetInnerHTML={{ __html : product.description}} variant ="body2" color="textSecondary" /> */}

            </CardContent>
            
        </Card>
    )
}

export default Product
