import React, {useState, useEffect} from 'react'
import { Link } from 'react-router-dom';
import { commerce } from '../../lib/commerce';
import { Typography, Button, Grid, Hidden } from '@material-ui/core';
import HomeCarousel from './HomeCarousel/HomeCarousel';
import HomeCategory from './HomeCategory/HomeCategory';

import Products from '../Products/Products'
import useStyles from './styles'
const HomePage = ({onAddToCart}) => {
    const classes = useStyles();
    const [newHomeProducts, setNewHomeProducts] = useState({});
    const [isLoading, setIsLoading] = useState(true)
    const fetchNewHomeProducts = async ()=> {
        setIsLoading(true)
        try{
            const {data} = await commerce.products.list({
                category_slug: ['homebasic'],
            });
            setNewHomeProducts(data); 
        }
        catch (error) {
            console.error();
        }
        finally{
            setIsLoading(false) 
        }
         
    }
    const InstagramImg = [2,3,4]

    
    useEffect( ()=>{
        fetchNewHomeProducts(); 
        return fetchNewHomeProducts();
    }, []);
    return (
        
        <>  
            <div className={classes.toolbar}/>
            <HomeCarousel typoDisplay='Adorn your Inner World'/>
            <HomeCategory />

            <div style={{textAlign: 'center'}}>
                <Typography variant='h4' color='primary' align='center' style={{marginTop: '2em'}}># NEWCOZYHOME</Typography>
                <Typography variant='overline'display='block'align='center'>Fresh items for your new home look.</Typography>
                {isLoading ?  null : <Products products={newHomeProducts} onAddToCart={onAddToCart}/>  }
                <Button component={Link} to='/Shop' variant='outlined' color='primary'>See Our Inventory</Button>
            </div>

            <div style={{textAlign: 'center'}}>
                <Typography variant='h4' color='primary' align='center' style={{marginTop: '2em'}}>#FOLLOW US ON INSTAGRAM</Typography>
                <Typography variant='overline'display='block'align='center'>Fresh on Stories</Typography>
                <Grid container style={{justifyContent: 'center'}} spacing={2}>
                    <Grid item xs={10} md={3}>
                        <img style={{maxWidth : '100%'}} src={process.env.PUBLIC_URL + `/assets/Instagram/1.jpg`} alt='follow us on instagram' />
                    </Grid>
                    <Hidden smDown>
                    {InstagramImg.map((item) => (
                    <Grid item md={3}>
                        <img style={{maxWidth : '100%'}} src={process.env.PUBLIC_URL + `/assets/Instagram/${item}.jpg`} alt='follow us on instagram' />
                    </Grid>
                    ))}
                    </Hidden>
                </Grid>
            </div>
            
        </>
        
    )
}

export default HomePage
