import React, {useState} from 'react'
import { Link, useLocation } from 'react-router-dom';
import { AppBar, Toolbar, IconButton, Badge, MenuItem,MenuList, Menu, Typography, Button, Paper, ClickAwayListener, Popper, Grow } from '@material-ui/core';
import { ShoppingCart } from '@material-ui/icons';
import logo from '../../../assets/logo.png'
import useStyles from './styles';

const DesktopNavBar = ({totalItems}) => {
    const classes = useStyles();
    const location = useLocation();
    const [open, setOpen] = React.useState(false);
    const anchorRef = React.useRef(null);
  
    const handleToggle = () => {
      setOpen((prevOpen) => !prevOpen);
    };
  
    const handleClose = (event) => {
      if (anchorRef.current && anchorRef.current.contains(event.target)) {
        return;
      }
  
      setOpen(false);
    };
  
    function handleListKeyDown(event) {
      if (event.key === 'Tab') {
        event.preventDefault();
        setOpen(false);
      }
    }
  
    // return focus to the button when we transitioned from !open -> open
    const prevOpen = React.useRef(open);
    React.useEffect(() => {
      if (prevOpen.current === true && open === false) {
        anchorRef.current.focus();
      }
  
      prevOpen.current = open;
    }, [open]);
  

    return (
      <>
        <AppBar position="fixed" className = {classes.appBar} color="inherit">
        <Toolbar>
            <Typography component={Link} to="/" variant="h5" className={classes.title} color="inherit">
                <img src={logo} alt="Logo" height='55px' className={classes.image} />
            </Typography>

            <div className={classes.grow}> 
                <Button className={classes.navEl} component={Link} to="/">Home</Button>
                <Button className={classes.navEl} component={Link} to="/About">About Team</Button>
                <Button
                className={classes.navEl}
                ref={anchorRef}
                aria-controls={open ? 'menu-list-grow' : undefined}
                aria-haspopup="true"
                onClick={handleToggle}
                >
                Shop
                </Button>
                <Popper open={open} anchorEl={anchorRef.current} role={undefined} transition disablePortal>
                {({ TransitionProps, placement }) => (
                    <Grow
                    {...TransitionProps}
                    style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
                    >
                    <Paper>
                        <ClickAwayListener onClickAway={handleClose}>
                        <MenuList className={classes.MenuList} autoFocusItem={open} id="menu-list-grow" onKeyDown={handleListKeyDown}>
                            <MenuItem onClick={handleClose} component={Link} to="/Shop/Livingroom">Living Room</MenuItem>
                            <MenuItem onClick={handleClose} component={Link} to="/Shop/Bedroom">Bedroom</MenuItem>
                            <MenuItem onClick={handleClose} component={Link} to="/Shop/Kitchen">Kitchen</MenuItem>
                            <MenuItem onClick={handleClose} component={Link} to="/Shop/Bathroom">Bathroom</MenuItem>
                        </MenuList>
                        </ClickAwayListener>
                    </Paper>
                    </Grow>
                )}
                </Popper>
                <Button className={classes.navEl} component={Link} to="/Contact">Contact</Button>
            </div>

            {location.pathname === "/cart" ? null : (
            <div className={classes.button}> 
                <IconButton component={Link} to="/cart" aria-label="Show cart items" color="inherit">
                    <Badge badgeContent={totalItems} color="secondary">
                        <ShoppingCart />
                    </Badge>
                </IconButton>
            </div>
            )}
        </Toolbar>
        </AppBar>
      </>
    )
}

export default DesktopNavBar
