import { makeStyles  } from "@material-ui/core/styles";

export default makeStyles(() => ({
    root: {
        maxWidth: '100%',
        boxShadow: "none",
        
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9 Ratio
    },
    cardActions: {
        display: 'flex',
        justifyContent: 'start',
        color: "red",
    },
    cardContent: {
        display: 'flex',
        justifyContent: 'space-between',
        color: "#6b705c",
        "&:hover": {
            color: "#cb997e",    
        },
    },
    
}));