import { makeStyles } from '@material-ui/core/styles';

export default makeStyles((theme) => ({
  toolbar: theme.mixins.toolbar,
  spinner: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)'
  },
  mainShop: {
    textAlign: 'center',
    position: 'relative'
  },
  drawerItems: {
    textAlign: 'center',
    marginTop: '11em',
    '& button' : {
        fontSize: '1.3em'
    }
  },
  drawer: {
    textAlign: 'center'
  }

  
}));